.scoreboard {
    background-color: #053C5E;
    display: flex;
    justify-content: space-around;
    height: 15%;
}

.scoreboard-entry {
    background-color: #1f7a8c;
    /* margin: 20px; */
    height: 25%;
    padding: 10px 20px;
    border-radius: 5px;
    border: 5px solid white;
    box-shadow: 4px 4px 0px black;
}