.taskbar {
    background-color: #053C5E;
    display: flex;
    align-items: center;
    /* position: absolute;
    bottom: 0px;
    width: 100%; */
    height: 15%;
    z-index: 1;
}

.word-preview {
    height: 38.5px;
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin: auto;
    width: 50%;
}

.word-preview:focus {
    outline: none;
}

.btn {
    background-color: #1f7a8c;
    margin: 20px;
    padding: 10px 20px;
    border-radius: 5px;
    border: 5px solid white;
    font-weight: bold;
    font-size: 16px;
    width: 125px;
    font-family: inherit;
    color: inherit;
    text-shadow: inherit;
    box-shadow: 4px 4px 0px black;
    letter-spacing: 0.05rem;
}

.btn:hover {
    background-color: #1A6675;
}

.btn:active {
    background-color: #165764;
}

.submit-btn {
    background-color: #DB222A;
}

.submit-btn:hover {
    background-color: #C21E26;
}

.submit-btn:active {
    background-color: #B01C23;
}